import { LoggerService } from '@nestjs/common'
import { inject, injectable } from 'inversify'
import * as qs from 'qs'
import { isLikeNumber, matchPath } from '../../utils/matchPath'
import { SchemaService } from './schema.service'
import * as _ from 'lodash'
import { IResourceSchema } from '../../interfaces/schema'
import { TFilterFormValue } from '@flowda-projects/flowda-shared'

const seperate = _.repeat('-', 50)

@injectable()
export class PrismaSchemaService {
  private readonly logger

  constructor(
    @inject(SchemaService) private schemaService: SchemaService,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(PrismaSchemaService.name)
  }

  toPrismaSelect(fields: string) {
    return fields.split(',').reduce((acc: Record<string, boolean>, cur: string) => {
      acc[cur] = true
      return acc
    }, {})
  }

  toFindParam(
    pathname: string,
    query: qs.ParsedQs,
  ):
    | {
        action: 'findUnique' | 'findMany'
        resource: string
        param: any
      }
    | Record<string, never> {
    if (!query.fields) {
      throw new Error('No query fields')
    }

    this.logger.log(seperate)
    this.logger.log(`pathname: ${pathname}, query: ${JSON.stringify(query)}`)

    const parsedPath = matchPath(pathname)
    if (parsedPath.length === 0) return {}

    const { resource, id, resourceSchema } = parsedPath[parsedPath.length - 1]

    const schemaCache = this.schemaService.getSchemaCache()
    const theResourceSchema = schemaCache[resourceSchema]

    let action: 'findUnique' | 'findMany'
    let param = {}

    const queryFields = query.fields as qs.ParsedQs
    const fields = this.toPrismaSelect(queryFields[resource] as string)

    const include: Record<string, any> = {}

    if (typeof query.include === 'string' && query.include !== '') {
      query.include.split(',').forEach((inc: string) => {
        // this.logger.log(`[toFindParam] parse include ${inc}`)
        const refSelect: any = this.getRefSelect(theResourceSchema, inc)
        const selectRet = this.toPrismaSelect(queryFields[inc] as string)
        include[inc] = {
          // todo:  prisma nest select  order by  include  include  nest select fields
          // orderBy: [{ createdAt: 'desc' }],
          select: { ...selectRet, ...refSelect },
        }
      })
    }

    if (id != null) {
      action = 'findUnique'
      param = {
        where: {
          id: id,
        },
        select: { ...fields, ...include },
      }
    } else {
      /*
      [
      {
        type: { eq: 'UNSCHEDULE' },
        status: { eq: 'DONE' },
      },
    ]
      */
      const filter = this.convertQueryToPrismaFilter(theResourceSchema, query)

      const sort: string = query.sort as string
      let orderBy
      if (!sort) {
        orderBy = [{ createdAt: 'desc' }]
      } else {
        if (sort[0] === '-') {
          orderBy = [{ [sort.slice(1)]: 'desc' }]
        } else {
          orderBy = [{ [sort]: 'asc' }]
        }
      }

      action = 'findMany'
      if (parsedPath.length > 1) {
        // 1 resource id  list
        const pResource = parsedPath[parsedPath.length - 2]
        // this.logger.log(`${resource}.findMany`)
        param = {
          where: {
            ...{
              [`${pResource.resource}Id`]: pResource.id,
              isDeleted: false,
            },
            ...filter,
          },
          orderBy,
          select: { ...fields, ...include },
        }
      } else {
        param = {
          where: {
            ...{
              isDeleted: false,
            },
            ...filter,
          },
          orderBy,
          select: { ...fields, ...include },
        }
      }
    }
    const ret = {
      action,
      param,
      resource,
    }
    this.logger.log(JSON.stringify(ret))
    this.logger.log(seperate)
    return ret
  }

  /**
   *  resource  schema  columns  ref e.g. resource(Receipt)  ref(partVersion)
   *  refSchema  display_column  include e.g. display_column(partId) nest select
   * { [include: partVersion]: { select { partId: true, [partId x-relationField: part]: { select: { id: true, [display_column*]: true} }}} }
   *  display_column* 
   *
   * todo:  display_column  ref  ref 
   * @param resourceSchema
   * @param includeRef
   */
  getRefSelect(resourceSchema: Partial<IResourceSchema>, includeRef: string) {
    const refSelect: any = {}
    const schemaCache = this.schemaService.getSchemaCache()
    if (resourceSchema && resourceSchema.columns) {
      // e.g. inc partVersion
      const refColumn = resourceSchema.columns.find(
        col => col.column_type === 'reference' && col.reference!['x-relationField'] === includeRef,
      )
      if (refColumn) {
        // e.g. model_name PartVersion
        // e.g. display_column partId,version
        const { model_name, display_column } = refColumn.reference!
        // e.g. PartVersionResourceSchema
        const refSchema = schemaCache[model_name + 'ResourceSchema']
        let displayCols: string[] = []
        if (typeof display_column === 'string') {
          displayCols = [display_column]
        } else {
          displayCols = display_column as string[]
        }
        displayCols.forEach(item => {
          // e.g. item partId
          const disCol = refSchema.columns!.find(col => col.name === item)
          if (disCol!.column_type === 'reference') {
            // e.g. name
            const display_column = disCol!.reference!.display_column!
            const relationField = disCol!.reference!['x-relationField']
            // todo: 
            // todo:  display_column fallback_display_column
            let display_column2: string
            if (Array.isArray(display_column)) {
              display_column2 = display_column[display_column.length - 1]
            } else {
              display_column2 = display_column
            }
            refSelect[relationField] = {
              select: {
                id: true,
                [display_column2]: true,
              },
            }
          }
        })
      }
    }
    return refSelect
  }

  convertQueryToPrismaFilter(resourceSchema: Partial<IResourceSchema>, query: any) {
    if (query.filter && Array.isArray(query.filter) && query.filter.length > 0) {
      // console.log(query.filter)
      const filter = query.filter as TFilterFormValue[]
      const andIdx = filter.findIndex(item => typeof item === 'string' && item === 'AND')
      const orIdx = filter.findIndex(item => typeof item === 'string' && item === 'OR')
      const ret: any = {}

      if (andIdx === 0) {
        ret['AND'] = []
        if (orIdx === -1) {
          const andFilter = filter.slice(1)
          andFilter.forEach(item => ret['AND'].push(this.mapItemToPrismaFilter(resourceSchema, item)))
        } else {
          const andFilter = filter.slice(1, orIdx)
          andFilter.forEach(item => ret['AND'].push(this.mapItemToPrismaFilter(resourceSchema, item)))
          ret['OR'] = []
          const orFilter = filter.slice(orIdx + 1)
          orFilter.forEach(item => ret['OR'].push(this.mapItemToPrismaFilter(resourceSchema, item)))
        }
      } else if (orIdx === 0) {
        ret['OR'] = []
        if (andIdx === -1) {
          const orFilter = filter.slice(1)
          orFilter.forEach(item => ret['OR'].push(this.mapItemToPrismaFilter(resourceSchema, item)))
        } else {
          const orFilter = filter.slice(1, andIdx)
          orFilter.forEach(item => ret['OR'].push(this.mapItemToPrismaFilter(resourceSchema, item)))
          ret['AND'] = []
          const andFilter = filter.slice(andIdx + 1)
          andFilter.forEach(item => ret['AND'].push(this.mapItemToPrismaFilter(resourceSchema, item)))
        }
      } else {
        throw new Error('Wrong filter')
      }
      return ret
    } else {
      return {}
    }
  }

  toUpdateParam(pathname: string, values: any) {
    this.logger.log(seperate)
    this.logger.log(`pathname: ${pathname}, body: ${JSON.stringify(values)}`)

    const matchRet = matchPath(pathname)
    const { resource, id, resourceSchema } = matchRet[matchRet.length - 1]
    this.removeRelationFields(resourceSchema, values)
    const ret = {
      resource,
      param: {
        where: { id: id },
        data: values,
      },
    }
    this.logger.log(JSON.stringify(ret))
    this.logger.log(seperate)
    return ret
  }

  mapItemToPrismaFilter(resourceSchema: Partial<IResourceSchema>, item: any) {
    const schemaCache = this.schemaService.getSchemaCache()
    // 
    const k = Object.keys(item)[0]
    // https://javascript.plainenglish.io/how-to-rename-object-keys-in-react-javascript-using-lodash-b73fb92ea24d
    item[k] = _.mapKeys(item[k], (v, k) => {
      switch (k) {
        case 'eq':
          return 'equals'
        case 'neq':
          return 'not'
        default:
          return k
      }
    })

    item[k] = _.mapValues(item[k], v => {
      //  schema  string  LIKE
      const kk = k.split('.')
      let col
      if (kk.length === 2) {
        const refCol = resourceSchema.columns!.find(col => {
          return col.column_type === 'reference' && col.reference!['x-relationField'] === kk[0]
        })
        const refSchema = schemaCache[refCol!.reference!.model_name + 'ResourceSchema']
        col = refSchema.columns!.find(item => item.name === kk[1])
      } else {
        col = resourceSchema.columns!.find(item => item.name === k)
      }
      if (col && ['string', 'textarea'].indexOf(col.column_type!) > -1) {
        return v
      } else if (isLikeNumber(v)) {
        return _.toNumber(v)
      } else {
        return v
      }
    })

    //  . chatGPT 
    const ret = {}
    _.forEach(item, (value, key) => {
      _.set(ret, key.replace(/\./g, '.'), value)
    })
    return ret
  }

  toCreateParam(pathname: string, values: any) {
    const matchRet = matchPath(pathname)
    // console.log(matchRet)
    const { resource, resourceSchema } = matchRet[matchRet.length - 1]
    this.removeRelationFields(resourceSchema, values)
    const schemaCache = this.schemaService.getSchemaCache()
    const theResourceSchema = schemaCache[resourceSchema]
    // console.log(theResourceSchema)

    const uniqueCols = theResourceSchema.columns!.filter(col => {
      return col.column_type === 'reference' && col.reference!['x-unique']
    })

    if (uniqueCols.length === 0) {
      return {
        action: 'create',
        resource: resource,
        param: {
          data: values,
        },
      }
    } else if (uniqueCols.length > 1) {
      throw new Error('Not support multiple unique key')
    } else {
      const uniqueCol = uniqueCols[0]
      // console.log(uniqueCol)
      return {
        action: 'update',
        resource: resource,
        param: {
          data: values,
        },
        'x-unique': uniqueCol,
      }
    }
  }

  removeRelationFields(resourceSchema: string, values: any) {
    const schemaCache = this.schemaService.getSchemaCache()
    // todo:  x-relationField  put  reference 
    const relationFields: string[] = []
    if (schemaCache) {
      const theResourceSchema = schemaCache[resourceSchema]
      // console.log(theResourceSchema)
      if (theResourceSchema) {
        Object.keys(values).forEach((k: string) => {
          const kProp = theResourceSchema.columns && theResourceSchema.columns.find(col => col.name === k)
          if (kProp && kProp.column_type === 'reference') {
            const relationField = kProp.reference?.['x-relationField']
            if (relationField) {
              relationFields.push(relationField)
            }
          }
        })
      }
    }
    // console.log(relationFields)
    relationFields.forEach(k => {
      delete values[k]
    })
  }
}
