import { inject, injectable } from 'inversify'
import { LoggerService } from '@nestjs/common'

@injectable()
export class AuthService {
  private readonly logger

  constructor(@inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService) {
    this.logger = loggerFactory(AuthService.name)
  }

  payload: any = null

  setPayload(payload: any) {
    if (payload == null) {
      this.logger.log(`set payload ${this.payload} to null`)
      this.payload = null
    } else {
      if (this.payload == null) {
        this.logger.log(`set payload to ${JSON.stringify(payload, null, 2)}`)
        this.payload = payload
      } else {
        // noop 
      }
    }
  }
}
