import { Body, Controller, Get, Post, Query, Req } from '@nestjs/common'

import { SchemaService } from '@flowda-projects/flowda-shared'
import { CustomService, UserService } from '@flowda-projects/wms-services'
import * as express from 'express'
import {
  QueryTableFilterSchemaDto,
  RemoveTableFilterSchemaDto,
  SaveTableFilterSchemaDto,
  TableFilterService,
} from '@flowda-projects/flowda-shared-node'

@Controller('/apps')
export class AppController {
  constructor(
    private readonly schema: SchemaService,
    private readonly user: UserService,
    private readonly custom: CustomService,
    private readonly tableFilter: TableFilterService,
  ) {}

  @Get('/__hi')
  hi() {
    return { hi: 'world' }
  }

  @Get('/getSchema')
  getSchema() {
    return this.schema.getSchema()
  }

  @Get('/findByUsername')
  findByUsername(@Query('username') username: string) {
    return this.user.findByUsername(username)
  }

  @Get('/getProductLineAndEquipment')
  getProductLineAndEquipment() {
    return this.custom.getProductLineAndEquipment()
  }

  @Get('/getEquipmentList')
  getEquipmentList(@Req() req: express.Request) {
    return this.custom.getEquipmentList(req.query)
  }

  @Get('/getEquipment')
  getEquipment(@Req() req: express.Request) {
    return this.custom.getEquipment(req.query)
  }

  @Get('/_test')
  test() {
    return this.custom.test()
  }

  @Post('/saveTableFilter')
  saveTableFilter(@Body() dto: SaveTableFilterSchemaDto) {
    return this.tableFilter.save(dto)
  }

  @Post('/queryTableFilter')
  queryTableFilter(@Body() dto: QueryTableFilterSchemaDto) {
    return this.tableFilter.query(dto)
  }

  @Post('/removeTableFilter')
  removeTableFilter(@Body() dto: RemoveTableFilterSchemaDto) {
    return this.tableFilter.remove(dto)
  }
}
