import { ContainerModule, interfaces } from 'inversify'
import { z } from 'zod'

import { bindService, ServiceSymbol } from '@flowda-projects/flowda-shared'
import { PrismaSchemaService } from './services/schema/prismaSchema.service'
import { DataService } from './services/data/data.service'
import { SchemaService } from './services/schema/schema.service'
import { SchemaTransformer } from './services/schema/schemaTransformer'
import { AuthService } from './services/auth/auth.service'

export const flowdaSharedModule = new ContainerModule((bind: interfaces.Bind) => {
  bindService(bind, ServiceSymbol, DataService)
  bindService(bind, ServiceSymbol, SchemaService)
  bindService(bind, ServiceSymbol, AuthService)

  bind(PrismaSchemaService).toSelf().inSingletonScope()

  bind(SchemaTransformer).toSelf().inTransientScope()
  bind<interfaces.Factory<SchemaTransformer>>('Factory<SchemaTransformer>').toFactory<SchemaTransformer, [any]>(
    context => {
      return (z: z.AnyZodObject) => {
        const transformer = context.container.get(SchemaTransformer)
        transformer.setZodType(z)
        return transformer
      }
    },
  )
})
