import {
  EquipmentWithRelationsSchema,
  IncomingInspectionRecordItemWithRelationsSchema,
  IncomingInspectionRecordWithRelationsSchema,
  IncomingInspectionSpecItemWithRelationsSchema,
  IncomingInspectionSpecWithRelationsSchema,
  NonconformItemWithRelationsSchema,
  OperationInspectionRecordItemWithRelationsSchema,
  OperationInspectionRecordWithRelationsSchema,
  PartOperationInspectionItemWithRelationsSchema,
  PartOperationWithRelationsSchema,
  PartVersionWithRelationsSchema,
  PartWithRelationsSchema,
  ProductLineWithRelationsSchema,
  ReceiptWithRelationsSchema,
  RepairMaterialInventorySchema,
  RepairRecordWithRelationsSchema,
  TaskFormRelationSchema,
  UserSchema,
  WorkerOrderWithRelationsSchema,
} from '@flowda-projects/prisma-wms'
import { meta } from '@flowda-projects/flowda-shared'
import { z } from 'zod'

export const UserResourceSchema = UserSchema.omit({
  hashedPassword: true,
  hashedRefreshToken: true,
}).extend({
  __meta: meta({
    extends: 'UserSchema',
  }),
})

export const TaskFormRelationResourceSchema = TaskFormRelationSchema.extend({
  __meta: meta({
    extends: 'TaskFormRelationSchema',
  }),
})

// prisma:false camunda API
export const TaskResourceSchema = z
  .object({
    id: z.string().cuid(),
    name: z.string(),
    assignee: z.string(),
    created: z.date(),
    executionId: z.string(),
    processDefinitionId: z.string(),
    processInstanceId: z.string(),
    taskDefinitionKey: z.string(),
    variables: z.string(),
  })
  .extend({
    __meta: meta({
      prisma: false,
    }),
  })
  .openapi({
    primary_key: 'id',
    display_name: '',
    display_column: 'name',
    display_primary_key: 'true',
  })

export const ProcessDefinitionResourceSchema = z
  .object({
    id: z.string(),
    key: z.string(),
    name: z.string(),
    version: z.number(),
    resource: z.string(),
    deploymentId: z.string().cuid(),
    suspended: z.boolean(),
    historyTimeToLive: z.number(),
    startableInTasklist: z.boolean(),
  })
  .extend({
    __meta: meta({
      prisma: false,
    }),
  })
  .openapi({
    primary_key: 'id',
    display_name: '',
    display_column: 'name',
    display_primary_key: 'true',
  })

export const WorkflowUserResourceSchema = z
  .object({
    id: z.string(),
    firstName: z.string(),
    lastName: z.string(),
    email: z.string(),
  })
  .extend({
    __meta: meta({
      prisma: false,
    }),
  })
  .openapi({
    primary_key: 'id',
    display_name: '',
    display_column: 'id',
  })

// custom form
export const AddToAdminResourceSchema = UserSchema.omit({
  hashedPassword: true,
  hashedRefreshToken: true,
  status: true,
  createdAt: true,
  updatedAt: true,
  username: true,
})
  .extend({
    uid: z.number().openapi({ title: ' ID', access_type: 'read_only' }),
    username: z.string().openapi({ title: '', access_type: 'read_only' }),
    __meta: meta({
      extends: 'UserSchema',
    }),
  })
  .openapi({
    primary_key: 'id',
    display_name: '',
    display_column: 'username',
    display_primary_key: 'true',
  })

// 
export const ManagerApproveSchema = z
  .object({
    comment: z.number().openapi({ title: '' }),
    __meta: meta({
      prisma: false,
    }),
  })
  .openapi({})

