import { inject, injectable } from 'inversify'
import * as db from '@prisma/client-wms'
import { createZodDto } from 'nestjs-zod'
import { TableFilterSchema } from '@flowda-projects/prisma-wms'
import { PrismaClientSymbol } from '@flowda-projects/flowda-shared'
import { LoggerService } from '@nestjs/common'

// todo:  schema  shared design 
const SaveTableFilterSchema = TableFilterSchema.omit({
  id: true,
  createdAt: true,
  updatedAt: true,
  isDeleted: true,
})

const QueryTableFilterSchema = TableFilterSchema.pick({
  path: true,
})

const RemoveTableFilterSchema = TableFilterSchema.pick({
  id: true,
})

export class SaveTableFilterSchemaDto extends createZodDto(SaveTableFilterSchema) {}

export class QueryTableFilterSchemaDto extends createZodDto(QueryTableFilterSchema) {}

export class RemoveTableFilterSchemaDto extends createZodDto(RemoveTableFilterSchema) {}

@injectable()
export class TableFilterService {
  private readonly logger

  constructor(
    // todo: 
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(TableFilterService.name)
  }

  save(dto: SaveTableFilterSchemaDto) {
    return this.prisma.tableFilter.create({
      data: dto,
    })
  }

  remove(dto: RemoveTableFilterSchemaDto) {
    return this.prisma.tableFilter.delete({
      where: { id: dto.id },
    })
  }

  query(dto: QueryTableFilterSchemaDto) {
    return this.prisma.tableFilter.findMany({
      where: {
        isDeleted: false,
        path: dto.path,
      },
      select: {
        id: true,
        path: true,
        name: true,
        filterJSON: true,
      },
    })
  }
}
