import { inject, injectable } from 'inversify'
import { DataService, PrismaClientSymbol } from '@flowda-projects/flowda-shared'
import { LoggerService } from '@nestjs/common'
import * as db from '@prisma/client-wms'
import * as _ from 'lodash'

@injectable()
export class CustomService {
  private readonly logger

  constructor(
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject(DataService) private data: DataService,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(CustomService.name)
  }

  getProductLineAndEquipment() {
    return this.prisma.productLine.findMany({
      where: {
        isDeleted: false,
      },
      orderBy: {
        createdAt: 'desc',
      },
      include: {
        equipment: {
          where: {
            isDeleted: false,
          },
          orderBy: {
            createdAt: 'desc',
          },
        },
      },
    })
  }

  async getEquipmentList(query: Record<string, any>) {
    const data = await this.data.get(query['path'], query)
    const ids = data.map((item: any) => item.id)
    const records = await this.prisma.repairRecord.findMany({
      where: {
        equipmentId: { in: ids },
      },
    })
    const groupedData = _.groupBy(records, 'equipmentId')
    this.logger.log('----- data -----', groupedData)
    const ret = data.map((item: any) => {
      const records = groupedData[String(item.id)]
      item.status = _.some(records, item => item.status !== db.RepairRecordStatus.DONE) ? '' : ''
      return item
    })

    return ret
  }

  async getEquipment(query: Record<string, any>) {
    const data = await this.data.get(query['path'], query)
    const records = await this.prisma.repairRecord.findMany({
      where: {
        equipmentId: data.id,
      },
    })
    data.status = _.some(records, item => item.status !== db.RepairRecordStatus.DONE) ? '' : ''
    return data
  }

  test() {
    return this.prisma.repairRecord.findMany({
      orderBy: [{ createdAt: 'desc' }],
      select: {
        id: true,
        type: true,
        status: true,
        equipmentId: true,
        description: true,
        createdAt: true,
        updatedAt: true,
        equipment: {
          select: {
            id: true,
            createdAt: true,
            updatedAt: true,
            name: true,
            description: true,
            repairPlan: true,
            workStation: true,
            productLineId: true,
          },
        },
      },
      where: {
        isDeleted: false,
        AND: [
          {
            equipment: {
              name: {
                contains: '1',
              },
            },
          },
        ],
      },
    })
  }
}
